let goodbyeInterval = null;
const goodbyeElement =
  '<div id="goodbye" class="flex justify-center items-center z-50 fixed top-0 left-0 w-screen h-screen bg-stone-200 dark:bg-stone-900 text-stone-800 dark:text-stone-300 transition-all font-heading text-7xl">Goodbye</div>';
const goodbyeList = [
  "Goodbye",
  "Hejdå",
  "Farvel",
  "Näkemiin",
  "Au revoir",
  "Tschüss",
  "Aloha",
  "Ciao",
  "Adiós",
  "وداعا",
  "さようなら",
  "再见",
  "Kwaheri",
  "ביי",
  "अलविदा",
  "잘 가",
  "Αντίο",
  "Adéu",
];

const addGoodbyeElement = () => {
  document.querySelector("body").innerHTML += goodbyeElement;
  goodbyeInterval = setInterval(() => {
    document.getElementById("goodbye").innerHTML =
      goodbyeList[Math.floor(Math.random() * goodbyeList.length)];
  }, 500);
  document
    .getElementById("goodbye")
    .addEventListener("mouseover", removeGoodbyeElement);
};

const removeGoodbyeElement = () => {
  document
    .getElementById("goodbye")
    .removeEventListener("mouseover", removeGoodbyeElement);
  document.getElementById("goodbye").remove();
  clearInterval(goodbyeInterval);
};

document.addEventListener("mouseleave", addGoodbyeElement);
